.container {
  padding-left: 0px;
  padding-right: 0px;
  width: 100%;
  margin: 0 auto;
}

@media (max-width:575px) {
  .container{
    max-width: 364px
  }
}

@media (min-width:576px) {
  .container{
    max-width: 540px
  }
}

@media (min-width:768px) {
  .container {
    max-width: 720px
  }
}

@media (min-width:992px) {
  .container {
    max-width: 960px
  }
}

@media (min-width:1200px) {
  .container {
    max-width: 1140px
  }
}

@media (min-width:1536px) {
  .container {
    max-width: 1320px
  }
}

@font-face {
  font-family: blackBubble;
  src: url(black-bubble.ttf);
}

.egg-menu-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
}

.title-bar {
  display: flex;
  align-items: center;
  min-height: 70px;
  padding: 10px 0;
  background-color: rgba(43,22,55,255);
}

.title-bar .content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 780px) {
  .title-bar .content {
    flex-direction: column;
  }
}

.menu-bar {
  display: flex;
  align-items: center;
  height: 50px;
  background-color: rgba(59,30,76,255);
}

.menu-bar .content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-bar .menu-item {
  color: white;
  margin: 0 15px;
  transition: all 0.25s;
  cursor: pointer;
  transform: scale(0.9);
}

.menu-bar .menu-item.active {
  transform: scale(1.1);
}

.menu-bar .menu-item:hover {
  transform: scale(1.1);
}

.menu-bar .menu-item.disabled {
  color: grey;
  pointer-events: none;
}

.main-content {  
  display: flex;
  width:  100%;
  justify-content: center;
  align-items: center;

  position: relative;
  width: 100%;
  flex-grow: 1;
  background: rgb(43,22,55);
  background: linear-gradient(180deg, rgba(86,40,105,255) 0%, rgba(7,0,8,255) 96%);
}

